@import "vars";
@import "media";

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  &::-webkit-scrollbar {
    width: 4px;
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    background-color: $gray-100;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $primary-500;
    border-radius: 5px;
  }
}

html {
  font-size: 16px;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  font: 400 16px/26px $open-sans;
  color: $gray-900;
  background-color: $gray-100;
  letter-spacing: 0;
}

.container {
  width: 1264px;
  max-width: 100%;
  padding: 0 32px;
  margin-left: auto;
  margin-right: auto;

  @include max-mobile {
    padding: 0 16px;
  }
}

.mb-128 {
  margin-bottom: $sections-gap-desk;

  @include max-tablet {
    margin-bottom: $sections-gap-tab;
  }

  @include max-mobile {
    margin-bottom: $sections-gap-mob;
  }
}

.mb-80 {
  margin-bottom: $sections-gap-mob;
}

.mb-40 {
  margin-bottom: 40px;
}

.mb-24 {
  margin-bottom: 24px;
}

video {
  border: 0;
  outline: none;
}

img {
  outline: none;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
  transition: $transition-hover;
}

.d-none {
  display: none !important;
}

.d-block {
  display: block !important;
}

.d-flex {
  display: flex !important;
}

.list-style-none {
  list-style: none;
}

.text-center {
  text-align: center;
}