.product-descriptions {
  display: flex;
  align-items: center;
  list-style-type: none;
  gap: 24px;

  &__item {
    padding-right: 24px;
    border-right: 1px solid $gray-0;

    &:last-child {
      border-right: 0;
      padding-right: 0;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }
}