.contact-us {
  display: flex;
  align-items: stretch;
  gap: 16px 24px;
  position: relative;

  @include max-tablet {
    flex-direction: column;
  }

  @include max-tablet {
    gap: 12px;
  }

  &__link {
    color: $primary-500;
    text-decoration: underline;
    font-weight: 600;
  }

  &__content {
    width: 100%;
    max-width: 336px;
    border-radius: 12px;
    background-color: $primary-500;
    padding: 24px;

    &--light {
      border: 1px solid $gray-0;
      background-color: hsla(0, 0%, 100%, .3);
      box-shadow: 0 8px 8px -4px rgba(16, 24, 40, .03), 0 20px 24px -4px rgba(16, 24, 40, .08);
      backdrop-filter: blur(5px);
    }

    @include max-tablet {
      max-width: unset;
    }
  }

  &__text {
    color: $gray-0;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__info {
    @include max-tablet {
      display: flex;
      flex-wrap: wrap;
      gap: 12px 40px;
    }

    @include max-mobile {
      flex-direction: column;
      gap: 0;
    }
  }

  &__info-item {
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    color: $gray-0;

    &:not(:last-child) {
      margin-bottom: 8px;

      @include max-tablet {
        margin-bottom: 0;
      }

      @include max-mobile {
        margin-bottom: 8px;
      }
    }

    &.email,
    &.phone {
      position: relative;
      padding-left: 30px;

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 2px;
        width: 20px;
        height: 20px;
      }
    }

    &.email {
      &:before {
        background: url(../img/icons/email.svg) no-repeat 50% 50% / 20px 20px;
      }
    }

    &.phone {
      &:before {
        background: url(../img/icons/tel.svg) no-repeat 50% 50% / 20px 20px;
      }
    }
  }

  &__info-link {
    &:hover {
      text-decoration: underline;
    }
  }

  &__form {
    flex-grow: 1;
  }

  &__anchor {
    position: absolute;
    top: -75px;
    left: 0;
  }
}