.benefit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(296px, 1fr));
  grid-gap: 24px;

  @include max-tablet {
    grid-gap: 16px;
  }

  @include max-mobile {
    grid-gap: 8px;
  }

  &__card {
    border-radius: 12px;
    background-color: $gray-030;
    border: 1px solid $gray-030;
    box-shadow: $shadow-lg;
    backdrop-filter: $shadow-blur;
    padding: 24px;
    transition: border-color 0.3s;

    &:hover {
      border-color: $gray-0;
    }
  }

  &__icon-box {
    display: flex;
    width: 56px;
    height: 56px;
    margin-bottom: 10px;
  }

  &__icon {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    display: block;
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;
    color: $gray-900;
    margin-bottom: 10px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    color: $gray-500;
  }

  &__link {
    font-weight: 600;

    &:hover {
      text-decoration: underline;
    }
  }
}
