.tabs {
  width: 100%;
  display: flex;
  align-items: stretch;
  gap: 24px;

  @include max-tablet {
    flex-direction: column;
  }

  &__list {
    width: 32%;
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include max-tablet {
      width: 100%;
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}

.tab {
  cursor: pointer;
  padding: 20px 24px;
  transition: $transition-hover;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
  text-transform: uppercase;

  @include max-tablet {
    padding: 12px 20px;
  }

  &.active {
    background-color: $primary-500;
    border-color: $primary-500;
    color: $gray-0;
  }

  &,
  &__content {
    border-radius: 12px;
    border: 1px solid $gray-0;
    background: $gray-030;
    box-shadow: $shadow-xl;
    backdrop-filter: $shadow-blur;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 67%;
    padding: 24px;
    min-height: 250px;

    @include max-tablet {
      width: 100%;
    }

    &:not(.active) {
      display: none;
    }

    .title {
      color: $gray-900;
      margin-bottom: 12px;
    }
  }

  &__content-holder {
    position: relative;
    flex: 1;
  }

  &__content-text {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    padding-right: 10px;

    @include max-tablet {
      position: static;
    }
  }

  &__content-paragraphs {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  &__links {
    display: flex;
    gap: 32px;
    margin-top: 24px;
  }
}