.fleet {
  &__content {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -70%) scale(0);
      border-radius: 70%;
      opacity: 0;
      background-color: $primary-500;
      filter: blur(28px);
      z-index: 1;
    }

    @include max-mobile {
      transform: scale(0.7);
    }
  }

  &__product-descriptions {
    position: relative;
    justify-content: center;
    opacity: 0;
    margin-top: 16px;
    z-index: 3;
  }

  .swiper-slide-active {
    overflow: visible !important;

    .fleet-slider__img {
      scale: (1.1);
    }

    .fleet__content {
      &:after {
        opacity: 0.2;
        transform: translate(-50%, -70%) scale(1.5, 1.1);
      }
    }

    .fleet__product-descriptions {
      opacity: 1;
    }
  }

  &__img {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 2;
  }

  &__btn-prev,
  &__btn-next {
    flex-shrink: 0;
  }

  &__nav-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;

    position: relative;
    width: 100%;
    max-width: 1200px;
    margin: 32px auto 0;

    @include medium-desktop {
      padding: 0 32px;
    }

    @include max-mobile {
      padding: 0 16px;
    }
  }

  &__slider-nav {
    max-width: calc(100% - 108px);
    overflow-x: hidden;

    .swiper-slide {
      padding: 24px;
      border-radius: 12px;
      border: 1px solid $gray-0;
      background-color: $gray-030;
      box-shadow: $shadow-xl;
      backdrop-filter: $shadow-blur;
      cursor: pointer;

      &:hover {
        border-color: $primary-500;
      }

      &.swiper-slide-thumb-active {
        border: 1px solid $primary-500;
      }
    }
  }

  &__slider-nav-img-box {
    width: 64px;
    height: 34px;
    margin: 0 auto 12px;
  }

  &__slider-nav-img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__slider-nav-text {
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    text-align: center;
  }
}
