.contact-us-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url("../img/bg-big.png") no-repeat center;
  background-size: cover;
  padding-top: 78px;
  margin-top: -48px;

  @include max-tablet {
    height: auto;
  }

  .title {
    &:before {
      z-index: 1;
    }
  }

  &__text {
    color: $gray-0;
    margin-bottom: 32px;
  }
}