.overview {
  &__list {
    border: 1px solid $white;
    border-radius: 12px;
    box-shadow: $shadow-xl;
    margin-bottom: 24px;
  }

  &__row {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 26px 24px;
    border-top: 1px solid $white;

    &:first-child {
      border-top: 0;
    }
  }

  &__row-heading {
    width: 130px;
    font-size: 14px;
    line-height: 24px;
  }

  &__row-holder {
    flex: 1;
  }

  &__row-value {
    font: 400 18px/28px $oswald;
    text-transform: uppercase;
  }

  &__items {
    display: flex;
    gap: 16px;

    @include max-tablet {
      gap: 0;
    }
    @include max-mobile {
      flex-wrap: wrap;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 131px;
  }

  &__item-text {
    font: 500 12px/16px $open-sans;
  }

  &__holder {
    display: flex;
    gap: 24px;

    @include max-tablet {
      flex-direction: column;
    }
  }

  &__map {
    height: 310px;
    flex: 1;
    border-radius: 12px;
    overflow: hidden;

    @include max-tablet {
      height: 276px;
      min-height: 276px;
    }
  }

  &__contacts {
    .contact-us {
      &__info-item {
        font-weight: 400;
      }

      &__info {
        flex-direction: column;
        row-gap: 12px;
      }
    }
  }
}