.description-section {
  display: flex;
  align-items: center;
  gap: 40px;
  padding: 40px;
  background-color: $primary-500;
  color: $gray-0;
  border-radius: 12px;
  box-shadow: $shadow-xl;
  backdrop-filter: blur(10px);

  @include max-tablet {
    flex-direction: column;
    gap: 24px;
  }

  @include max-mobile {
    padding: 24px;
  }

  &__article {
    width: 62%;

    @include max-tablet {
      width: 100%;
    }
  }

  &__text {
    margin: 24px 0 32px;
    display: flex;
    flex-direction: column;
    gap: 12px;

    ul {
      padding-left: 24px;
      display: flex;
      flex-direction: column;
      gap: 12px;
    }
  }

  &__image {
    display: flex;
    width: 34%;

    @include max-tablet {
      width: 100%;
      max-height: 400px;
    }

    @include max-mobile {
      max-height: 270px;
    }

    img {
      object-fit: contain;
      margin: 0 auto;
    }
  }
}