.footer {
  background: $primary-900;
  padding: 32px 0;
  color: $gray-0;

  &__logo {
    display: flex;
    width: 130px;

    @include max-mobile {
      width: 136px;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    line-height: 24px;

    &:nth-child(1) {
      row-gap: 16px;
    }

    &:nth-child(3) {
      gap: 12px 30px;
    }

    ul {
      list-style: none;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px 24px;
  }

  &__socials-item {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  &__nav {
    align-items: start;
    border-top: 1px solid rgba(255, 255, 255, 0.20);
    border-bottom: 1px solid rgba(255, 255, 255, 0.20);
    padding: 32px 0;
    margin: 32px 0;

    @include max-mobile {
      gap: 32px;
      justify-content: flex-start;
      padding: 24px 0;
      margin: 24px 0;
    }

    &-title {
      font: 400 18px/28px $oswald;
      text-transform: uppercase;
      margin-bottom: 16px;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    &-column {
      @include max-mobile {
        min-width: 40%;
        flex: 1;
      }
    }
  }

  &__link,
  &__socials-item {
    transition: $transition-hover;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }
}