.blog-posts {
  position: relative;

  &__link {
    position: absolute;
    top: 32px;
    right: 32px;

    @include max-mobile {
      right: 16px;
      padding: 8px 20px;
    }
  }

  &__categories {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    margin-bottom: 24px;

    @include max-mobile {
      gap: 16px;
    }
  }

  &__pagination {
    display: flex;
    gap: 24px;
    width: fit-content;
    margin: 48px auto 0;

    .btn--secondary-stroke {
      display: flex;
      padding: 8px;
    }
  }

  &__pagination-items {
    display: flex;
    gap: 8px;

    .btn--tetriary {
      padding: 8px 16px;
      color: $gray-500;
      font-weight: 400;

      &:hover,
      &:focus {
        color: $gray-0;
      }
    }
  }
}

.blog-page {
  &__title {
    text-align: center;
  }
}