.page {
  display: flex;
  flex-direction: column;
  align-content: center;
  min-height: 100vh;

  &__main {
    flex: 1;
    max-width: 100%;
    overflow: hidden;
    padding: 112px 0 0;

    &.overflow-visible {
      overflow: visible;
    }
  }
}

.page-header {
  margin-bottom: 48px;
}