.blog-cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 24px;

  @include max-tablet {
    gap: 16px;
  }

  @include max-mobile {
    flex-direction: column;
    gap: 8px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: calc(33.3% - 16px);
    border-radius: 12px;
    border: 1px solid $gray-0;
    background: $gray-030;
    box-shadow: $shadow-xl;
    backdrop-filter: $shadow-blur;
    overflow: hidden;
    transition: $transition-hover;

    @include max-tablet {
      width: calc(50% - 8px);
    }

    @include max-mobile {
      width: 100%;
    }

    &:hover,
    &:focus {
      background-color: $gray-070;
    }

    &-image {
      width: 100%;
      height: 200px;
      object-fit: cover;
    }

    &-description {
      padding: 24px;
      display: flex;
      flex-direction: column;
      gap: 8px;
    }

    &-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-type,
    &-date {
      color: $gray-500;
      font-size: 14px;
      line-height: 24px;
    }

    &-type {
      font-weight: 500;
      text-transform: uppercase;
    }

    &-title {
      font-size: 18px;
      font-weight: 500;
      line-height: 28px;
    }

    &-text {
      color: $gray-500;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }
}